<template>
    <section class="main-content" id="tabs">
        <loader :is-visible="isLoading"></loader>
        <div class="padding-t_10" id="reportstabsdata">
            <div class="left-menu-tab">
                <div class="col-md-12 p-0 ">
                    <div class="theme-primary partition">
                        <span class="p-name text-white">{{ Headerstext }}</span>
                    </div>
                </div>

                <ul class="nav nav-pills flex-column  left-tab mt-2 responivecustomtab nav-tabs"
                    style="cursor:pointer;">
                    <li v-for="tab in tabs" :key="tab.id" @click="activeTab = tab.id, changeTab(tab.id)"
                        :class="{ 'nav-item': true, 'active': activeTab === tab.id }">
                        <span class="nav-link left-menu" :class="{ 'active': activeTab === tab.id }">
                            <i class="fa fa-envelope" v-if="tab.id === 1"></i>
                            <i class="fa fa-calendar" v-if="tab.id === 2"></i>
                            <i class="fa fa-circle-o-notch" v-if="tab.id === 3"></i>
                            {{ tab.title }}
                        </span>
                    </li>
                </ul>
            </div>
            <div class="right-content">
                <div id="ReturnRequestHeading" class="theme-primary partition  p-actions-expand"><span
                        class="p-name text-white">{{ navheading }}</span> </div>
                <div class="col-md-12 p-0" v-for="tab in tabs" :key="tab.id" v-show="activeTab === tab.id">

                    <div class="col-lg-12 border float-left py-3">
                        <div class="row">

                            <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left ml-4">
                                <div class="row mt-0">
                                    <div class="col-7 float-left pr-1 pl-0">
                                        <div class="form-group">
                                            <!-- <label>search:</label><span class="text-danger"></span> -->
                                            <Field :rules="{ 'required': false }" class="w-100" name="Reader" v-slot="{ errors }">
                                                <input class="form-control" type="text" placeholder="Barcode" v-model="barcodeNumber" autocomplete="off" name="search">
                                            </Field>
                                        </div>
                                    </div>
                                    <div class="col-5 float-left pl-0">
                                        <div class="background-0 text-left form-check-inline">
                                            <em id="Go" v-on:click="Search()" title="Search" href="javascript:;"
                                                class="btn btn-success mr-1 px-4 hlsearch mr-2">
                                                <i class="fa fa-search pr-2"></i>
                                            </em>
                                            <em title="clear" v-on:click="Clear()" href="javascript:;"
                                                class="btn btn-danger clearsearchtext px-4">
                                                <i class="fa fa-refresh pr-2"></i>
                                            </em>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div class="col-12 mb-2 px-0 ml-2" v-if="tab.id != 1">
                            <div class="mr-2 d-inline-block">
                                <span class="colorboxsquare" style="background-color: red;"></span> Overdue
                            </div>
                        </div>
                    </div>

                    <div class="tab-content mb-2">
                        <div class="expand-list">
                            <em href="javascript:;" class="expand-list-btn justify-content-center align-items-center">
                                <i class="icon-solid arrow-solid-left" style="font-size:9pt"></i>
                            </em>
                        </div>

                        <div>

                            <div class="listing" v-if="tab.id == 1">
                                <div class="table-responsive" v-if="noRecord == false">

                                    <div class="p-2 border-bottom">
                                        <div class="row">
                                            <div class="col-lg-6">
                                                <div class="row mx-0">
                                                    <div class="col-lg-4"><b class="font-weight-medium">{{
            $t("ItemName") }}:</b></div>
                                                    <div class="col-lg-8">{{ ProductInformation.catalogName }}</div>
                                                </div>
                                            </div>
                                            <div class="col-lg-6">
                                                <div class="row mx-0">
                                                    <div class="col-lg-4"><b class="font-weight-medium">{{
            $t("UniqueName") }}:</b></div>
                                                    <div class="col-lg-8">{{ ProductInformation.uniqueName }}</div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="p-2 border-bottom">
                                        <div class="row">
                                            <div class="col-lg-6">
                                                <div class="row mx-0">
                                                    <div class="col-lg-4"><b class="font-weight-medium">{{
            $t("SerialNumber") }}:</b></div>
                                                    <div class="col-lg-8">{{ ProductInformation.serialNumber }}</div>
                                                </div>
                                            </div>
                                            <div class="col-lg-6">
                                                <div class="row mx-0">
                                                    <div class="col-lg-4"><b class="font-weight-medium">{{
            $t("Brand") }}:</b></div>
                                                    <div class="col-lg-8">{{ ProductInformation.brand }}</div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="p-2 border-bottom">
                                        <div class="row">
                                            <div class="col-lg-6">
                                                <div class="row mx-0">
                                                    <div class="col-lg-4"><b class="font-weight-medium">{{
            $t("ModelName") }}:</b></div>
                                                    <div class="col-lg-8">{{ ProductInformation.modelName }}</div>
                                                </div>
                                            </div>
                                            <div class="col-lg-6">
                                                <div class="row mx-0">
                                                    <div class="col-lg-4"><b class="font-weight-medium">{{
            $t("ModelNumber") }}:</b></div>
                                                    <div class="col-lg-8">{{ ProductInformation.modelNumber }}</div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="p-2 border-bottom">
                                        <div class="row">
                                            <div class="col-lg-6">
                                                <div class="row mx-0">
                                                    <div class="col-lg-4"><b class="font-weight-medium">{{ $t("Cost")
                                                            }}:</b></div>
                                                    <div class="col-lg-8" v-if="ProductInformation.cost == null">0.00
                                                    </div>
                                                    <div class="col-lg-8" v-else>{{ ProductInformation.cost }}</div>
                                                </div>
                                            </div>
                                            <div class="col-lg-6">
                                                <div class="row mx-0">
                                                    <div class="col-lg-4"><b class="font-weight-medium">{{
            $t("WarrantyDuration") }}:</b></div>
                                                    <div class="col-lg-8">{{ ProductInformation.warrantyDuration }}</div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="p-2 border-bottom">
                                        <div class="row">
                                            <div class="col-lg-6">
                                                <div class="row mx-0">
                                                    <div class="col-lg-4"><b class="font-weight-medium">{{
            $t("WarrantyExpirationDate") }}:</b></div>
                                                    <div class="col-lg-8">{{ ProductInformation.warrantyExpirationDate }}
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>

                                </div>
                                <div v-else class="table-responsive">
                                    <p class="text-center text-danger p-2 m-0">{{ $t('NoRecordfound') }}</p>
                                </div>

                            </div>

                            <div class="listing mt-2" v-if="tab.id == 2">
                                <tg-list :showCheckBox="false" :IsShowAction="false" :listType="['List']"
                                    ModuleName="Asset" SubModuleCode="Asset" IdentityColumn="ASSET_CATALOGUE_ID"
                                    :PageSize="pageSize" HeaderText="Asset Management" :widgets="widgets"
                                    :ListData="PendingRequestData" :HeaderData="Header"
                                    :callbackfunction="RequsitionPending" :LegendArray="LegendArray"
                                    :SearchFields="leftSearchFields" :SortExp="SortExp" :SortBy="SortBy"
                                    :NorecordfoundText="$t('NorecordfoundText')" :TotalRecords="TotalRecord"
                                    :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="RequsitionPending"
                                    v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
                                    v-on:SortdataButtonClick="sortdata">

                                    <template v-slot:slotdata="{ data }">
                                        <template v-if="data.column.COLUMN_NAME == 'FROM_TIME'">
                                            <span class="text-dark center-icon" :title="data.column.COLUMN_NAME">
                                                {{ $options.filters.formatDateTime(data.row.FROM_TIME) }}
                                            </span>
                                        </template>
                                        <template v-if="data.column.COLUMN_NAME == 'TO_TIME'">
                                            <span class="text-dark center-icon" :title="data.column.COLUMN_NAME">
                                                {{ $options.filters.formatDateTime(data.row.TO_TIME) }}
                                            </span>
                                        </template>
                                        <template v-if="data.column.COLUMN_NAME == 'CREATED_AT'">
                                            <span class="text-dark center-icon" :title="data.column.COLUMN_NAME">
                                                {{ $options.filters.formatDateTime(data.row.CREATED_AT) }}
                                            </span>
                                        </template>
                                        <template v-if="data.column.COLUMN_NAME == 'IS_ASSIGNED'">
                                            <span class="text-dark center-icon" :title="data.column.COLUMN_NAME">
                                                <em class="fa fa-reply fa-lg action-icon text-success"
                                                    v-on:click="IsAssigned(data.row)"></em>
                                            </span>
                                        </template>
                                        <template v-if="data.column.COLUMN_NAME == 'Reject'">
                                            <span class="text-dark center-icon" :title="data.column.COLUMN_NAME">
                                                <em class="fa fa-times-circle fa-lg action-icon text-danger"
                                                    v-on:click="RequsitionReject(data.row)"></em>
                                            </span>
                                        </template>
                                    </template>
                                </tg-list>

                            </div>

                            <div class="listing mb-3" v-if="tab.id == 3">
                                <tg-list ref="tgList" :IsShowAction="true" :showCheckBox="false" :listType="['List']"
                                    ModuleName="Asset" :PageSize="PageSize" SubModuleCode="PRODUCT_ASSIGNMENT"
                                    :ViewList="ViewList" v-on:ApplyView="OnApplyView"
                                    IdentityColumn="assignment_detail_id" HeaderText="Product Assignment"
                                    :ListData="AssetAssignmentData" :IsFilterApplied="searchCondition ? true : false"
                                    :IsSearchFieldsOnTop="false" :HeaderData="Headers" :widgets="widgets"
                                    :callbackfunction="FetchData" :TotalRecords="TotalRecords"
                                    :SearchFields="leftSearchFields" :NorecordfoundText="$t('NoRecordfound')"
                                    :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData"
                                    :LegendArray="LegendArray" :SortExp="SortExp" :SortBy="SortBy"
                                    v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
                                    v-on:HeaderButtonClick="commonHeaderButtonClick"
                                    v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata"
                                    v-on:RefreshManageView="FetchData">
                                    <template v-slot:slotdata="{ data }">
                                        <!-- {{ data.row }} -->
                                        <template v-if="data.column.COLUMN_NAME == 'unique_name'">
                                            <span v-if="data.row.create_unique_name_list == false" class="text-dark"
                                                title="">

                                            </span>
                                            <span v-else class="text-dark" :title="data.row.unique_name">
                                                {{ data.row.unique_name }}

                                            </span>
                                        </template>

                                        <template v-if="data.column.COLUMN_NAME == 'number_of_items_assigned'">
                                            <span v-if="data.row.create_unique_name_list == false" class="text-dark">
                                                <i class="fa fa-list-ul" aria-hidden="true"></i>
                                                <span class="noti-circle noti-blue" v-if="data.row.is_return">{{data.row.return_request_qty}}</span>
                                                <span class="noti-circle noti-blue" v-else>{{data.row.number_of_items_assigned }}</span>
                                            </span>
                                            <span v-else class="text-dark">
                                                <i class="fa fa-list-ul" aria-hidden="true"></i><span
                                                    class="noti-circle noti-blue">
                                                    {{ data.row.number_of_items_assigned }} </span>
                                            </span>
                                        </template>

                                        <template v-if="data.column.COLUMN_NAME == 'assigned_from'">
                                            <span class="text-dark"
                                                :title="$options.filters.formatDateTime(data.row.assigned_from)">
                                                {{ $options.filters.formatDateTime(data.row.assigned_from) }}
                                            </span>
                                        </template>

                                        <template v-if="data.column.COLUMN_NAME == 'assigned_till'">
                                            <span class="text-dark"
                                                :title="$options.filters.formatDateTime(data.row.assigned_till)">
                                                {{ $options.filters.formatDateTime(data.row.assigned_till) }}
                                            </span>
                                        </template>

                                        <template v-if="data.column.COLUMN_NAME == 'return_date'">
                                            <span class="text-dark"
                                                :title="$options.filters.formatDateTime(data.row.return_date)">
                                                {{ $options.filters.formatDateTime(data.row.return_date) }}
                                            </span>
                                        </template>

                                    </template>
                                </tg-list>

                            </div>
                        </div>
                    </div>
                    <div>
                    </div>
                </div>

            </div>

            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 text-right px-0 float-left">
                <em @click="previousTab" v-if="activeTab > 1" class="btn btn-success text-white mr-2" value="Previous"
                    title="Previous" name="Previous"><i
                        class="fa fa-angle-double-left pr-2"></i><span>Previous</span></em>
                <em @click="nextTab" v-if="activeTab < 3" value="Next" title="Next" name="btnNext"
                    class="btn btn-primary text-white"><span>Next</span><i
                        class="fa fa-angle-double-right pl-2"></i></em>
                <input type="hidden" id="getValue" value="ViewBag.Getvalue" />
            </div>

        </div>
        <RequsitionRejectModal v-if="IscheckReject" @ClosePOPuP="ClosePOPuP" :RejectData="RejectData">
        </RequsitionRejectModal>
    </section>
</template>

<script>
import DataService from '../../service/DataService';
import RequsitionRejectModal from '../../components/Modal/RequsitionReject.vue';

//import Schema from "../ProductAssignment/AssignmentSchema";

export default {
    Prop: {

    },
    components: {
        RequsitionRejectModal,
    },
    data() {
        return {
            relatedInfoID: '',
            barcodeNumber: '',
            ProductInformation: [],
            PendingRequestData: [],
            ProductAssignment: [],
            activeTab: 1,
            searchcondition: '',
            noRecord: true,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            SortBy: "",
            SortExp: "",
            pageSize: 10,
            pageNumber: 1,
            pageNum: 1,
            isLoading: false,
            IscheckReject: false,
            uIds: '',
            catId: '',
            batchId: '',
            tabs: [{
                id: 1,
                title: 'Product Information'
            },
            {
                id: 2,
                title: 'Pending Check Out List'
            },
            {
                id: 3,
                title: 'Product Assignment'
            },
            ],
            navheading: 'Product Information',
            Headerstext: this.$t('HeadingText'),
            Headers: [],
            AssetAssignmentData: [],
            Header: [{
                "COLUMN_NAME": "ITEM_UNIQUE_NAME",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("ProductName"),
                "DISPLAY_ORDER": 1,

            },
            {
                "COLUMN_NAME": "FROM_TIME",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("FromTime"),
                "DISPLAY_ORDER": 2,
                settings: {
                    isInSlot: true
                }
            },
            {
                "COLUMN_NAME": "TO_TIME",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("ToTime"),
                "DISPLAY_ORDER": 3,
                settings: {
                    isInSlot: true
                }
            },
            {
                "COLUMN_NAME": "USER_NAME",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("CheckOutRequestBy"),
                "DISPLAY_ORDER": 4
            },
            {
                "COLUMN_NAME": "CREATED_AT",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("CheckOutRequestDate"),
                "DISPLAY_ORDER": 5,
                settings: {
                    isInSlot: true
                }
            },

            {
                "COLUMN_NAME": "IS_ASSIGNED",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("Assign"),
                "DISPLAY_ORDER": 6,
                settings: {
                    isInSlot: true
                }
            },
            {
                "COLUMN_NAME": "Reject",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("Reject"),
                "DISPLAY_ORDER": 7,
                settings: {
                    isInSlot: true
                }
            },

            ],

        }
    },

    created: async function () {
        var vm = this;
        await vm.GetPageSize();
        vm.relatedInfoID = window.location.href.split('/')[5];
        vm.FetchData();
    },
    methods: {
        // async GetPageSize() {
        //     var vm = this;
        //     var params = 'key=DefaultPageSize'
        //     await DataService.GetConfigurationValueForCompanyByKey(params).then((response) => {
        //         if (response.data != null) {
        //             vm.pageSize = parseInt(response.data);
        //         }
        //     });
        // },
        Assign: function (ths) {
            this.$router.push({
                name: "AddAssetAssignment",
                params: {
                    reqForm: 'pendingRequest'
                }
            });
        },
        SortByClick: function (val, id) {
            var vm = this;
            if (id == 1 || id == 4) {
                vm.SortBy = val;
                vm.SortExp = vm.SortExp == 'ASC' ? 'DESC' : 'ASC';
                vm.FetchData();
            } else if (id == 2) {
                this.SortBy = val;
                vm.SortExp = vm.SortExp == 'ASC' ? 'DESC' : 'ASC';
                vm.GetAssetReplacement();
            } else {
                this.SortBy = val;
                vm.GetAssetReturnRequestList();
            }
        },
        changeTab(tab) {
            var vm = this;
            if (tab == 1) {
                vm.navheading = 'Product Information';
                vm.FetchData();
            } else if (tab == 2) {
                vm.navheading = 'Pending Check Out List';
                vm.SortBy = '';
                vm.SortExp = ''
                vm.RequsitionPending();
            } else {
                vm.navheading = 'Product Assignment';
                vm.SortBy = '';
                vm.SortExp = ''
                vm.ProductAssignments();
            }

        },
        previousTab() {
            if (this.activeTab > 1) {
                this.activeTab--;
                this.changeTab(this.activeTab);
            }
        },
        nextTab() {
            if (this.activeTab <= this.tabs.length - 1) {
                this.activeTab++;
                this.changeTab(this.activeTab);
            }
        },
        FetchData() {
            var vm = this;
            vm.isLoading = true;
            var Barcode = '';
            var RelatedInfoId = 0;
            if (vm.barcodeNumber != '' && vm.barcodeNumber != null) {
                Barcode = vm.barcodeNumber;
                RelatedInfoId = '';
                vm.isLoading = false;
            } else {
                RelatedInfoId = vm.relatedInfoID;
                Barcode = '';
                vm.isLoading = false;
            }
            var url = `barcodeNumber=${Barcode}&relatedInfoId=${RelatedInfoId}`;
            DataService.GetCatalogInfoByBarcode(url).then(response => {
                if (response.data != null) {
                    vm.pageSize;
                    vm.ProductInformation = response.data;
                    vm.noRecord = false;
                    vm.isLoading = false;
                } else {
                    vm.noRecord = true;
                    vm.ProductInformation = [];
                    vm.isLoading = false;
                }

            });
        },
        Search: function () {
            if (this.activeTab == 1) {
                this.FetchData();
            } else if (this.activeTab == 2) {
                this.RequsitionPending();
            } else {
                this.ProductAssignments();
            }
        },
        Clear: function () {
            this.barcodeNumber = '';
            this.changeTab(this.activeTab);
        },
        pagerMethod: function (value) {
            this.pageSize = value.pageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },

        RequsitionPending() {
            var vm = this;
            vm.isLoading = true;
            const params = this.getRequestParamsPending(
                this.pageNum,
                this.pageSize,
                this.sortBy,
                this.sortExp,
                this.uIds,
                this.catId,
                this.batchId,
                this.relatedInfoID,
                this.barcodeNumber
            );

            params.pageNum = vm.PageNum;

            if (vm.PageNum) {
                params.pageNum = vm.PageNum;
            } else {
                vm.PageNum = params.pageNum = 1;
            }
            params.pageSize = vm.pageSize;
            params.sortBy = vm.sortBy;
            params.sortExp = vm.sortBy;
            params.uIds = vm.uIds;
            params.catId = vm.catId;
            params.batchId = vm.batchId;
            if (vm.barcodeNumber != '' && vm.barcodeNumber != null) {
                params.barcode = vm.barcodeNumber;
                params.relatedInfoID = '';
            } else {
                params.searchCondition = '';
                params.relatedInfoID = vm.relatedInfoID;
            }

            //var url=`uIds=${vm.uIds}&catIds=${vm.catIds}&batchId=${vm.batchId}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&barcode=${vm.barcode}&type=${vm.type}`;
            DataService.RequsitionPendingList(params).then(response => {
                if (response.data.length > 2) {
                    vm.PendingRequestData = JSON.parse(response.data);
                    vm.PendingRequestData.forEach((row) => {
                        row.additionalClass = '';
                        if (row.ISEXPIRED) {
                            row.additionalClass = 'high-profile-bar';
                        }
                    });
                    vm.TotalRecord = vm.PendingRequestData[0].TOTAL_PAGES;
                    vm.TotalPage = Math.ceil(vm.TotalRecord / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.noRecord = false;
                    vm.isLoading = false;
                } else {
                    vm.noRecord = true;
                    vm.PendingRequestData = [];
                    vm.TotalRecord = 0
                    vm.isLoading = false;
                }
                setTimeout(function () {
                    vm.CheckBoxBootstrap();
                }, 100);
                setTimeout(function () {
                    vm.ResponsiveDataTable("tablelistingdata");
                }, 500);
            });

        },
        IsAssigned: function (item) {
            var vm = this;
            DataService.CheckPrivilege('controller=AssetAssignment&action=BarcodeSearch').then((response) => {
                if (response.data) {
                    this.$router.push({ name: "AddAssetAssignment", params: { reqForm: 'pendingRequest', items: item } });
                } else {
                    vm.ShowAlert(vm.$t('NotAuthorizedSection'), "failure", true, "Alert");

                }
            });
        },
        RequsitionReject: function (item) {
            var vm = this;
            DataService.CheckPrivilege('controller=AssetAssignment&action=BarcodeSearch').then((response) => {
                if (response.data) {
                    vm.IscheckReject = true;
                    vm.RejectData = item;
                } else {
                    vm.ShowAlert(vm.$t('NotAuthorizedSection'), "failure", true, "Alert");

                }
            });
        },
        ClosePOPuP: function () {
            var vm = this;
            vm.IscheckReject = false;
        },
        getRequestParamsPending: function (
            pageNum,
            pageSize,
            sortBy,
            sortExp,
            uIds,
            catId,
            batchId,
            relatedInfoID,
            barcodeNumber
        ) {
            let params = {};
            if (pageNum) {
                params["pageNum"] = pageNum;
            }
            if (pageSize) {
                params["pageSize"] = pageSize;
            }
            if (sortBy) {
                params["sortBy"] = sortBy;
            }
            if (sortExp) {
                params["sortExp"] = sortExp;
            }
            // debugger;
            if (uIds) {
                params["uIds"] = uIds;
            }
            if (catId) {
                params["catId"] = catId;
            }
            if (batchId) {
                params["batchId"] = batchId;
            }
            if (relatedInfoID) {
                params["relatedInfoId"] = relatedInfoID;
            }
            if (barcodeNumber) {
                params["barcode"] = barcodeNumber;
            }
            return params;
        },
        getRequestParams: function (pageNum, pageSize, sortBy, sortExp, isPaged, searchcondition, relatedInfoID, barcodeNumber) {
            let params = {};
            if (pageNum) { params["pageNum"] = pageNum; }
            if (searchcondition) { params["searchcondition"] = searchcondition; }
            if (isPaged) { params["isPaged"] = isPaged; }
            if (sortBy) { params["sortBy"] = sortBy; }
            if (sortExp) { params["sortExp"] = sortExp; }
            if (pageSize) { params["pageSize"] = pageSize; }
            if (relatedInfoID) { params["relatedInfoID"] = relatedInfoID; }
            if (barcodeNumber) { params["barcode"] = barcodeNumber; }
            return params;
        },
        async ProductAssignments() {
            var vm = this;
            vm.isLoading = true;

            //  var params = `pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&isReturn=&issuedTo=&assetCatalogId=${vm.assDetailIDs}&uniqueNames=&showWithoutPaging=&barcode=&UserStatus=&${vm.searchCondition}`
            const params = this.getRequestParams(this.pageNum, this.pageSize, this.sortBy, this.sortExp, this.isPaged, this.searchCondition, this.relatedInfoID, this.barcodeNumber);

            params.pageSize = vm.pageSize;
            params.pageNum = vm.PageNumber;
            params.searchcondition = vm.searchCondition;
            if (vm.PageNumber) { params.pageNum = vm.PageNumber; }
            else { vm.PageNumber = params.pageNum = 1; }
            if (typeof vm.isPaged != 'undefined') { params.isPaged = vm.isPaged; }
            else { vm.isPaged = params.isPaged = 1; }
            params.isPaged = vm.isPaged;
            params.sortBy = vm.SortBy;
            params.sortExp = vm.SortExp;
            params.assetCatalogId = vm.assDetailIDs

            var srchCondition = "";
            if (!this.isBlank(this.searchCondition)) {
                srchCondition = srchCondition + this.searchCondition;
            }
            if (!this.isBlank(this.conditionForInstantSearch)) {
                srchCondition = srchCondition + ((!this.isBlank(this.searchCondition)) ? (" AND " + this.conditionForInstantSearch) : this.conditionForInstantSearch);
            }
            if (srchCondition != "") {
                params.searchcondition = srchCondition;
                if (vm.appliedFilter.length > 0) {
                    const removeFilterindex = vm.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'removeFilter';
                    });
                    vm.listheaderbuttons[removeFilterindex].isvisible = true;
                    const openFilterPopUpindex = vm.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'openFilterPopUp';
                    });
                    vm.listheaderbuttons[openFilterPopUpindex].iconClass = 'fa fa-filter text-success';
                }
            }
            if (vm.barcodeNumber != "" && vm.barcodeNumber != null) {
                params.pageSize = 10;
                params.pageNum = 1;
                params.sortBy = '';
                params.sortExp = '';
                params.isReturn = '';
                params.issuedTo = '';
                params.issuedBy = '';
                params.assetCatalogId = '';
                params.uniqueNames = '';
                params.showWithoutPaging = '';
                params.barcode = vm.barcodeNumber;
                params.UserStatus = '';
                params.search = '';
                params.searchItemCode = '';
                params.Show = '';

            } else {
                params.searchCondition = '';
                params.uniqueNames = vm.relatedInfoID;
            }


            await DataService.AssetAssignmentListing(params).then((response) => {
                vm.isLoading = false;

                if (response.data != null && response.data != 0) {
                    vm.Headers = JSON.parse(response.data).schema;
                    console.log("headers", vm.Headers);

                    vm.Headers.forEach((column) => {

                        if (column.COLUMN_NAME == 'unique_name') {
                            column.settings = {
                                isInSlot: true,
                                isSortable: true
                            }
                        }
                        if (column.COLUMN_NAME == 'number_of_items_assigned') {
                            column.settings = {
                                isInSlot: true,
                                isSortable: true
                            }
                        }

                        if (column.COLUMN_NAME == 'assigned_from') {
                            column.DATA_TYPE = "text";
                            column.settings = {
                                isInSlot: true,
                                isSortable: true
                            }
                        }
                        if (column.COLUMN_NAME == 'assigned_till') {
                            column.DATA_TYPE = "text";
                            column.settings = {
                                isInSlot: true,
                                isSortable: true
                            }
                        }
                        if (column.COLUMN_NAME == 'return_date') {
                            column.DATA_TYPE = "text";
                            column.settings = {
                                isInSlot: true,
                                isSortable: true
                            }
                        }

                    });


                    if (JSON.parse(response.data).data.length > 0) {
                        vm.AssetAssignmentData = JSON.parse(response.data).data;
                        vm.AssetAssignmentData.forEach((row) => {
                            row.additionalClass = '';
                            var GetCurrentDate = new Date();
                            var IsOverdue = false;
                            //row.ASSIGNED_TILL != null ? ((row.IS_RETURN == false && vm.$options.filters.formatDate(row.ASSIGNED_TILL) < vm.$options.filters.formatDate(GetCurrentDate)) ? true : false) : false;
                            if (((vm.$options.filters.formatDateTimeYYMMDD(GetCurrentDate)) > (vm.$options.filters.formatDateTimeYYMMDD(row.assigned_till))) && row.is_return == false) {
                                IsOverdue = true;
                            } else {
                                IsOverdue = false;
                            }
                            if (IsOverdue) {
                                row.additionalClass = 'high-bar';
                            }
                        });
                        for (var row in vm.AssetAssignmentData) {
                            vm.AssetAssignmentData[row].isCheckBoxDisabled = false;
                            if (vm.AssetAssignmentData[row].is_return == true) {
                                vm.AssetAssignmentData[row].isCheckBoxDisabled = true;
                            }
                        }
                        vm.TotalRecords = JSON.parse(response.data).data[0].TOTAL;
                        vm.TotalPages = Math.ceil(
                            vm.TotalRecords / vm.PageSize
                        );
                        vm.CurrentPage = vm.PageNumber;
                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                        vm.noRecord = false;
                    } else {
                        vm.noRecord = true;
                        vm.AssetAssignmentData = [];
                        vm.TotalRecords = 0;
                        vm.isLoading = false;
                    }
                } else {
                    vm.noRecord = true;
                    vm.AssetAssignmentData = [];
                    vm.TotalRecords = 0;
                    vm.isLoading = false;
                }

            });
            vm.isLoading = false;
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            var vm = this;
            self.listActions.forEach((actionItem) => {
                let action = {
                    ...actionItem
                };
                switch (action.title) {
                    case "Share":
                        if (
                            (Boolean(row.create_unique_name_list) && !row.is_return && (vm.$options.filters.formatDateTimeMMDDYY(row.assigned_till) > vm.$options.filters.formatDateTimeMMDDYY(vm.UserDateTime)))
                        ) {
                            action.isDisabled = false;
                        } else {
                            action.isDisabled = true;
                        }

                        break;
                    case "Approve/Reject Request":
                        if (row.return_request.toUpperCase() == 'YES') {
                            if (row.is_return == false) {
                                action.isDisabled = false;
                                action.isVisible = true;
                            } else {
                                action.isDisabled = true;
                                action.isVisible = false;
                            }
                        } else if (row.return_request.toUpperCase() == 'NO') {
                            action.isDisabled = true;
                            action.isVisible = false;
                        }
                        break;

                    case "Returned":

                        if (row.return_request.toUpperCase() == 'YES') {
                            if (row.is_return == false) {
                                action.isDisabled = true;
                                action.isVisible = false;
                            } else {

                                action.isDisabled = false;
                                action.isVisible = true;
                                vm.isTrueForcheck = true;
                            }
                        } else if (row.return_request.toUpperCase() == 'NO') {
                            if (row.is_return == true) {
                                action.isDisabled = true;
                                action.isVisible = true;
                            } else {
                                action.isDisabled = true;
                                action.isVisible = false;
                            }
                        }

                        break;
                    case "Not Returned":
                        if (row.return_request.toUpperCase() == 'YES') {
                            if (row.is_return == true) {

                                if (vm.isTrueForcheck == true) {
                                    action.isDisabled = true;
                                    action.isVisible = false;
                                    break
                                }
                                else {
                                    action.isDisabled = false;
                                    action.isVisible = true;
                                }

                            } else {
                                action.isDisabled = true;
                                action.isVisible = false;
                            }
                        }
                        else {
                            if (row.is_return == true) {
                                action.isDisabled = true;
                                action.isVisible = false;

                            } else {
                                action.isDisabled = false;
                                action.isVisible = true;
                            }
                        }

                        break;

                    case "Request For Return":
                        if (row.is_return == false && row.return_request.toUpperCase() == "NO" && row.issued_to_id != vm.Userid) {
                            if (row.request_id == 0) {
                                action.isDisabled = false;
                                action.isVisible = true;
                            } else {
                                action.isDisabled = true;
                                return action.isVisible = false;
                            }
                        } else {
                            action.isDisabled = true;
                            action.isVisible = true;
                        }

                        break;
                    case "Request Already Sent":

                        if (row.is_return == false && row.return_request.toUpperCase() == "NO" && row.issued_to_id != vm.Userid) {
                            if (row.request_id == 0) {
                                action.isDisabled = true;
                                return action.isVisible = false;
                            } else {
                                action.isDisabled = false;
                                action.isVisible = true;
                            }
                        } else {
                            action.isDisabled = false;
                            action.isVisible = false;
                        }
                        break;
                    case "Comment":
                        if (row.is_return == true && (row.title != '' && row.title != null) || (row.comment != '' && row.comment != null)) {
                            action.isDisabled = false;
                        } else {
                            action.isDisabled = true;
                        }
                        break;
                    default: {
                        action.isDisabled = false;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        commonHeaderButtonClick() {
        },
    }
}
</script>

<style>
tr.high-profile-bar td:first-child::after {
    background: #de0303;
}
</style>
